import axios from 'axios';

let base = 'http://dev.angulartest';

//USER
export const requestLogin = params => { return axios.post(`${base}/login`, params).then(res => res.data); };

export const getUserListPage = params => { return axios.get(`${base}/frontend/users`, { params: params }); };

export const addUser = params => { return axios.post(`${base}/frontend/users`, params); };

export const removeUser = params => { return axios.delete(`${base}/frontend/users/${params.id}`, { params: params }); };

export const editUser = params => { return axios.put(`${base}/frontend/users/${params.id}`, params); };

//TYPEUSER
export const getTypeUserListPage = params => { return axios.get(`${base}/frontend/typeUser`) };

export const addTypeUser = params => { return axios.post(`${base}/frontend/typeUser`, params); };

export const removeTypeUser = params => { return axios.delete(`${base}/frontend/typeUser/${params.id}`, { params: params }); };

export const editTypeUser = params => { return axios.put(`${base}/frontend/typeUser/${params.id}`, params); };



